body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.animate {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

/*=== FADE IN  ===*/
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
.fadeIn {
    -webkit-animation-name: fadeIn;
	animation-name: fadeIn;
}
.container {
	height: 70vh;
	padding-left: 14em;
	display: -webkit-flex;
	display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Josefin Sans', sans-serif !important;
}

.centerButton {
	display: -webkit-flex;
	display: flex;
	
}

@media only screen and (max-width: 600px) {
  .container {
	width: 90%;
	height: 80vh;
	padding-left: 1.8em !important;
	margin-bottom: 5em !important;
	}
}

@media only screen and (max-width: 400px) {
	.container {
	  height: 90vh;
	  width: 90%;
	  }
  }

.container h1 {
	font: 700 80px 'Dancing Script', cursive !important;
}

@media only screen and (max-width: 800px) {
  .container h1 {
	font: 700 60px 'Dancing Script', cursive !important;
	}
}
.grow { 
	transition: all .2s ease-in-out; 
	
}

.grow:hover { 
	-webkit-transform: scale(1.1); 
	        transform: scale(1.1); 
}

.grow:hover .items,
.grow:focus .items {
  display: block;
}

.textover {
  color: white; /* Good thing we set a fallback color! */
}

.items {
    display: none;
    font: 700 60px 'Dancing Script', cursive !important;
	  color: white;
	  text-shadow: 3px 3px 0px rgba(0,0,0,0.6), 3px 3px 0px rgba(0,0,0,0.6) !important;
    text-align: center;
    margin: auto;
    position: absolute;
    top: 50%;
  	left: 50%;
  	-webkit-transform: translate(-50%, -50%);
  	        transform: translate(-50%, -50%);
    cursor: pointer;
}

 @media only screen and (max-width: 900px) {
  .items {
    display: block;
	}
}


